import React, {Component} from 'react';
import { Container,Tab, Row, Nav,Col, Image} from 'react-bootstrap';

import mole from '../assets/YouTubeScreen1.png';
import zombie from '../assets/zombiePost.png';

import icon from '../assets/getIcon.png';




export default class About extends Component {

  


    render() {
        return (

          

            <Container>
               <Tab.Container id="ledt-tabs-example" defaultActiveKey="first">
                <Row>
               <Col sm={3}>
                  
                  <Nav variant="pills" className = "flex-column mt-2" defaultActiveKey="/first">
                       <Nav.Item>
                              <Nav.Link eventKey="first">Sly Mole Fly: Digger of Garden</Nav.Link>
                       </Nav.Item>
                       <Nav.Item>
                              <Nav.Link eventKey="second">Zombie vs House Defender</Nav.Link>
                       </Nav.Item>
        
                     </Nav>
               
               </Col>

               <Col sm={9}>
               <Tab.Content className="mt-3">
               <Tab.Pane eventKey="first">

               <img
        className ="d-block w-100"
        src = {mole}
        alt =""
        
        />

             <p>
             <h6>Collect the whole crop, earn a little extra money and find out the secret that the mole keeps!</h6>
             
             <h4>Storyline</h4>
The sly mole pest lives under the garden, harvests the farmer's crops and sells them. Not a single vegetable or grain will leave the mole, its greed
and the ability to find anything knows no bounds. The farmer figured out how to get rid of an unwanted competitor: he stuck a hose with water
in his home in the hope that the mole will leave his garden.
But the animal is not as simple as it seems, it will be able not only to stop the flood, but at the same time and make good money by harvesting the entire crop. The farmer often rebuilds the irrigation system, and the mole's memory is not the best, so each new the level is fascinating and full of dangers.

             </p>

            
             <h1> </h1>        
            <div link = "market://details?id=com.PrimeStudio.SlyMoleFly">
            <a href="market://details?id=com.PrimeStudio.SlyMoleFly"><div><Image style={{width: 200, height: 'auto'}}  src={icon} alt=""  >
</Image></div></a>

</div>

            
        
               </Tab.Pane>
               <Tab.Pane eventKey="second">
               <img
        className ="d-block w-100"
        src = {zombie}
        alt =""
        
        />

            

             <p>
             <h6> Become a hero! Kill zombies and prevent zombie apocalypse in this adventure !</h6>

             <h4>Storyline</h4>
             There was an outbreak of a previously unknown disease in the city.
The whole world around is crumbling and only one former military man, retired USA army officer is trying to resist all this zombie chaos. He bravely stands guard over his home and will give a decisive rebuff to any zombie enemy who dares to challenge his homeland.

            
            

             </p>
             <h1> </h1>        
            <div link = "market://details?id=com.PrimeStudio.ZombieVsHouseDefender">
            <a href="market://details?id=com.PrimeStudio.ZombieVsHouseDefender"><div><Image style={{width: 200, height: 'auto'}}  src={icon} alt=""  >
</Image></div></a>

</div>


             
               </Tab.Pane>
              
               </Tab.Content>
               </Col>


                </Row>

               </Tab.Container>


            </Container>
        )
    }
}