import React, {Component} from 'react';
import Carousel from 'react-bootstrap/Carousel';
import winter from '../assets/slyPost.png';
import spring from '../assets/zombiePost.png';




    export default class CarouselBox extends Component {
    render() {
        return (
         <Carousel>
        <Carousel.Item>
        <img
        className ="d-block w-100"
        src = {winter}
        alt ="Forest"
        
        />

 <Carousel.Caption>
     <h3>   <p> <font size="6" color="#0000FF" face="serif">Sly Mole Fly: Digger of Garden</font></p></h3>
     <p><p> <font size="5" color="#0000FF" face="serif">If you don't know how to have fun - dig it!</font></p></p>
 </Carousel.Caption>

        </Carousel.Item>
        <Carousel.Item>
        <img
        className ="d-block w-100"
        src = {spring}
        alt ="Forest"
        
        />

 <Carousel.Caption>
     <h3><p> <font size="6" color="#FFFF00" face="serif">Zombie vs House Defender</font></p></h3>
     <p><p> <font size="5" color="#FFFF00" face="serif">Defend your Home!</font></p></p>
 </Carousel.Caption>

        </Carousel.Item>

         </Carousel>
            
        )
    }
}