import React, { Component } from 'react'
import { Navbar, Form, Container, Nav, NavLink, Button, FormControl } from 'react-bootstrap'
import logo from './logo192.png'


import {
    BrowserRouter as Router,
    Switch,
    Route,
    Link
  } from "react-router-dom";

import Home from '../Pages/Home.js'
import About from '../Pages/About.js'
import Contacts from '../Pages/Contacts.js'


export default class Header extends Component {

    render() {
        return (
            <>
                <Router>            
                <Navbar  collapseOnSelect expand="md" bg="dark" variant="dark">
                    <Container>
                        <Navbar.Brand href="/">
                            <img
                                src={logo}
                                height="100"
                                width="160"
                                className="d-inline-block align-top"
                                alt="logo"
                            /> 
                        </Navbar.Brand> 
                        <Navbar.Toggle aria-controls="responsive-navbar-nav" />
                        <Navbar.Collapse id="responsive-navbar-nav" >
                            <Nav className="mr-auto">
                                <NavLink as={Link} to ="/">  <p> <font size="5" color="#fa8e47" face="serif">Home</font></p>   </NavLink>
                                <NavLink as={Link} to="/about"><p> <font size="5" color="#fa8e47" face="serif">Games</font></p></NavLink>
                                <NavLink as={Link} to="/contacts"><p> <font size="5" color="#fa8e47" face="serif">Contacts</font></p></NavLink>
                              
                                
                            </Nav>
                            <Form inline>
                                <FormControl
                                    type="text"
                                    placeholder="Search"
                                    className="mr-sm-2"
                                />
                                <Button variant="outline-info">
                                    Search
                                </Button>
                            </Form>
                        </Navbar.Collapse>
                    </Container>
                </Navbar>

                
                    <Switch>
                        <Route exact path = "/" component={Home} />
                        <Route exact path = "/about" component={About} />
                        <Route exact path = "/contacts" component={Contacts} />
                       
                    </Switch>
                </Router>


            </>
        )
    }

}